@import "src/common/Styles/sass/variables";

.mainBlock {
  min-height: 100vh;
  background-color: $bgColor;
  padding-top: 200px;

  .text {
    width: 300px;
    position: relative;


    background-color: $bgColor;

    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;

    h4 {
      font-size: 15px;
      color: $primaryColor;
    }

    h2 {
      font-size: 30px;
      line-height: 42px;
      text-transform: capitalize;
      color: $textColor;
      margin: 0 0 8px;
      font-weight: 600;
      letter-spacing: -0.04em;

      span {
        color: $primaryColor;
        font-family: 'Poppins', sans-serif;
      }
    }

    .defaultBtn {
      text-decoration: none;
      font-family: "Poppins", sans-serif;
      letter-spacing: 0;
      background-color: $primaryColor;
      color: $bgColor;
      line-height: 45px;
      display: inline-block;
      padding: 0 25px;
      border-radius: 0;
      font-size: 14px;
      font-weight: 600;

      position: relative;
      overflow: hidden;
      z-index: 1;

      -webkit-transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      -moz-transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      -ms-transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      -o-transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
      transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    }

    p {
      margin: 20px 0;
      font-size: 15px;
      height: 100px;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 8px;
        height: 8px;
      }

      &::-webkit-scrollbar-thumb {
        cursor: pointer;
        background: #dc3545;
      }

      &::selection {
        background-color: #dc3545;
        color: #fff;
      }
    }
  }

  .photo {
    width: 430px;
    background-color: $bgColor;
    margin-top: 20px;

    .manPhoto {
      height: inherit;
      width: inherit;
    }
  }
}


