* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

*, ::after, ::before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Poppins', sans-serif;
    -webkit-font-smoothing: antialiased;
    color: #333;
}

h3, h4 {
    margin: 0 0 10px;
    font-weight: 600;
    line-height: 1.4;
    color: #022144;
    letter-spacing: -0.01em;
}


body {
    margin: 0;
    background-color: #fff;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    line-height: 26px;
    color: #808899;
    font-weight: 400;
    position: relative;
    overflow-x: hidden;
    /*overflow-y: hidden;*/
    /*scrollbar-base-color: #056ef1;*/
    /*scrollbar-arrow-color: #84ebff;*/
    /*-ms-scrollbar-track-color: #dc3545;*/
}

body::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

body::-webkit-scrollbar-thumb {
    cursor: pointer;
    background: #dc3545;
}

body::selection {
    background-color: #dc3545;
    color: #fff;
}
